import { call, put } from 'redux-saga/effects';
import { startLoading, finishLoading } from '../modules/loadingReducer';

export const createRequestActionTypes = (type) => {
  const SUCCESS = `${type}_SUCCESS`;
  const FAILURE = `${type}_FAILURE`;
  return [type, SUCCESS, FAILURE];
};

/**
 *  
 * @param {*} type : Reducer action type
 * @param {*} callAPI :   (ex: apiAuth.login)
 * @returns
 */
export default function createRequestSaga(type, callAPI) {
  const SUCCESS = `${type}_SUCCESS`;
  const FAILURE = `${type}_FAILURE`;

  /* yield */
  return function* (action) {
    console.log('action ==> ', action);
    //put -> action dispatch 
    yield put(startLoading(type)); //
    try {
      const response = yield call(callAPI, action.payload);
      // console.log('calllAPI:', callAPI, ',', response.data);
      console.log('response ==> ', response);
      //put ->  dispatch
      yield put({
        type: SUCCESS,
        payload: response.data,
        meta: response,
      });
    } catch (error) {
      console.log('error ====> ', error);
      yield put({
        type: FAILURE,
        payload: error,
        error: true,
      });
    }
    yield put(finishLoading(type)); // 
  };
}
