import React, { useEffect, useState } from 'react';
import { useDispatch, useSelector } from 'react-redux';
import { changeField, initializeForm, login } from '../../modules/authReducer';
import AuthForm from '../../components/auth/AuthForm';
import { check as memberCheck } from '../../modules/userReducer';
import { useNavigate } from 'react-router-dom';

const LoginForm = () => {
  const [error, setError] = useState(null);
  const navigater = useNavigate();
  //reducer action
  const dispatch = useDispatch();
  //reducer state
  const { form, auth, authError, user } = useSelector(
    ({ authReducer, userReducer }) => ({
      form: authReducer.login,
      auth: authReducer.auth,
      authError: authReducer.authError,
      user: userReducer.user,
    }),
  );
  //input event handler
  const onChange = (e) => {
    const { value, name } = e.target;
    dispatch(
      changeField({
        form: 'login',
        key: name,
        value,
      }),
    );
  };
  //form submit evnet handler
  const onSubmit = (e) => {
    e.preventDefault();
    const { username, password } = form;
    dispatch(login({ username, password }));
  };

  //form 
  useEffect(() => {
    dispatch(initializeForm('login'));
  }, [dispatch]);

  // 
  useEffect(() => {
    if (authError) {
      console.log(' ');
      console.log(authError);
      setError(' ');
      return;
    }
    if (auth) {
      console.log(' ');
      console.log(auth);
      dispatch(memberCheck());
    }
  }, [auth, authError, dispatch]);

  useEffect(() => {
    if (user) {
      navigater('/');
      try {
        localStorage.setItem('user', JSON.stringify(user));
      } catch (e) {
        console.log('localStorage is not working');
        throw e;
      }
    }
  }, [navigater, user]);

  return (
    <AuthForm
      type="login"
      form={form}
      onChange={onChange}
      onSubmit={onSubmit}
      error={error}
    />
  );
};

export default LoginForm;
